.midia {
  margin: 140px 100px;
  background-color: white;
  display: flex;
}
.midia-title {
  font-size: 3em;
  text-align: left;
  padding: 60px 70px 50px 80px;
  color: #444649;
  text-decoration: underline;
}
.midia-content {
  display: flex;
  flex-direction: column;
  flex: 6 1;
  padding-bottom: 60px;
}
.midias {
  display: flex;
  margin-left: 80px;
}
.videos-subtitle {
  font-size: 24px;
  color: rgb(110, 9, 9);
  font-weight: 600;
}
.videos {
  flex: 1 1;
}
.videos-text {
  font-size: 18px;
  color: #444649;
  font-weight: 600;
  padding: 30px 0 20px 0;
}
.videos-content {
